

const OnBaseProcess = () => {
    return (
        <div className='process-container'>
            <h1>OnBase Process</h1>
            


            

            <ol>
                <h3 id='Part 1: Thumbnail Viewer'>Part 1: Thumbnail Viewer</h3>
                <li>
                Search for the barcode in the Inbox using the input field under the 'Name' Column
                </li>
                <li>
                Select the Unity Form for the Work Order in the inbox to populate the 
                Related Items field with the documents contained in that Work Order.
                </li>
                <li>
                Left-Click on the first document in the Related items pane and scroll 
                to the bottom, then hold Shift while clicking on the last item in the list.
                This will select all the documents in the Work Order.
                    <ul>
                        <li>
                        Note: A shortcut to select all is by holding the Left Ctrl button
                         on the keyboard while pressing A. Be sure the focus is on the Related
                          Items pane rather than the Inbox by first clicking on any document in 
                          the Related Items pane.  
                        </li>
                    </ul>
                </li>
                <li>
                While all documents are selected, Right Click on the selection 
                and select “Open in Thumbnail Viewer” from the pop-up menu.
                </li>
                <li>
                	From the thumbnail viewer, scroll through all the documents to check 
                    that they are legible and that any documents indexed by the system are indexed correctly 
                    <br /> <br /> 
                    <ul>
                        <li>
                        In the case they are illegible – these documents will need to be Errored out.
                         See the Erroring a Document Process.
                        </li>
                        <li>
                        In the case they are mis-indexed – double click on the mis-indexed document 
                        to bring up the individual document in a new window
                        <ul>
                            <li>
                            In the top tool-bar, hit the Re-Index button to display the Re-Indexing Panel,
                             and follow the usual Re-indexing process before closing the window. 
                             For more details on the re-indexing process, see the Re-Indexing Workflow Process.
                            </li>
                        </ul>
                        </li>
                    </ul>
                </li>

                <h3 id='Part 2: Re-Indexing Workflow'>Part 2: Re-Indexing Workflow</h3>
                <li>
                Close the Thumbnail Viewer and select all Documents that are labelled
                    'AC-Maint – Unmatched OCR-T2PLXXXXXXXX – NXXXQS – IN PROCESS' 

                </li>
                <li>
                In the toolbar directly above the Related Items in the Related Items panel,
                 hit ‘Tasks’ and select ‘Re-Index Document’ from the drop-down menu. This will
                  bring up the ‘Workflow Re-Index’ Panel.
                </li>

                <li>
                Select the second drop down menu in the ‘Workflow Re-Index’ Panel labeled 
                ‘Document Type’ and choose the correct document type for the page displayed in
                 the Secondary Viewer.
                </li>
                <li>
                Hit Re-index at the bottom of the ‘Workflow Re-Index’ Panel to complete the
                 indexing of the current page. 
                </li>
                <li>
                This panel will iterate through each page until there are no additional documents
                 to re-index, so repeat Steps 8 and 9 until completion.
                 <br /> <br /> 
                 <ul>
                     <li>
                     Note: To abort the re-indexing process, click anywhere in OnBase outside of 
                     the ‘Workflow Re-Index’ Panel. This will save the re-indexing you’ve done up
                      until this point while aborting the process for the remaining documents.
                     </li>
                     <li>
                     To cancel re-indexing on a particular document without aborting the re-indexing 
                     process, simply hit the Cancel button next to the Re-Index button at the bottom 
                     of the “Workflow Re-index” Panel.
                     </li>
                 </ul>
                </li>
                <li>
                You can type the data into Maintinex by going through each Vendor page or FF-1 
                directly in OnBase and typing the data from the Secondary Viewer.
                <br /> <br />
                <ul>
                    <li>
                    An alternative is to print any relevant Vendor or FF-1 paperwork to a PDF. This is
                     done by selecting each Vendor Discrepancy and FF-1 in the Related Items Panel, 
                     Right Clicking, and then selecting Print under the ‘Send To’ dropdown menu in 
                     the pop up menu that appears. Select ScanSoft PDF Create! or Microsoft Print to PDF
                      as the Printing Device in the drop down menu and then hit the Print Button at the very top. 

                    </li>
                </ul>
                </li>

                <p>
                    *Note: To view the intermediate steps for data entry in Maintinex, view the 
                    <span style={{color: 'blue'}}> Data Entry in Maintinex Guide. </span>
                </p>
                <li>
                Upon completion of the Data Entry, return to OnBase and proceed to the
                 <span style={{color: 'blue'}}> Assigning Task Barcodes Guide. </span>
                </li>




            </ol>

        </div>
    )
}

export default OnBaseProcess
