

const OnBase = ( ) => {

    
    return (
        <div className='overview-container'>
            <h1 id='Overview'>Overview</h1>

            <p>
            OnBase provides us with a database of each Work Order that
            has been scanned in. Unity Forms, described below, house
            scans of the physical copies of the documents in its
            respective Work Order.

            </p>
            <h3 id='Unity Forms'> Unity Forms </h3>
            <p>
            The Work Orders are tracked within OnBase with a sheet known
            as a 
            <span style={{color: 'blue'}}> Unity Form.</span>
             This form contains basic data pertaining to
            the Work Order, such as the Work Order Barcode and Tail Number.
            The documents contained within a Work Order follow this form
            through the Queues. When searching for the correct Work Order 
            in the Inbox Panel, the panel is populated with the Unity Form
            that matches the barcode you searched for.
            </p>

            <h3 id='Workflow Queues'> Workflow Queues</h3>

            <p>
            As mentioned in the previous section, Work Orders follow their
            Unity Form through a series of Queues.  A general overview of 
            a Unity Form's lifecycle is as follows: <br /> <br />
            
            The Unity Form is created when the Work Order is scanned into 
            Onbase. The Form originates in the 
            <span style={{color: 'blue'}}> Data Admin Queue.</span>
            <br /> <br />

            When data is completed and the Unity Form is submitted in the 
            Data Admin Queue, it will proceed to either the Waiting Originals Queue
            (if the Work Order contains originals), or otherwise
            the QC Acceptance Queue. The QC Acceptance Queue is not one that is within
            our workscope. <br /> <br />

            If a Work Order was pushed into the Waiting Originals Queue, once submitted
            from that Queue it will be pushed into QC Acceptance as well.
            Eventually, all Queues lead to the final location known as
            <span style={{color: 'blue'}}> the Library.</span>
            <br /> <br />
           
            </p>
            <h4>Data Admin Queue</h4 >
            <p>
            The Data Admin Queue is where you will index each document within
            the Work Order before typing the data in MxNet and finally pushing
            the Work Order out of the Queue.
            </p>

            <h4>Waiting Originals Queue</h4>
            <p>
            The Waiting Originals Queue is where a Unity Form will proceed to
            under the condition that the attached Work Order contains Originals.
            When pushing Originals on mail day, you will be matching each
            physical copy of the Original from an Aircraft's folder with digital
            documents in OnBase under its Unity Form before pushing the Work Order
            out of this queue.
            </p>

            <h4>Document Separation Queue</h4>
            <p>
            The Document Separation Queue contains Work Orders that did not scan
            into OnBase correctly.  On occassion, the system incorrectly assigns
            an incomplete barcode, incorrect barcode, or several barcodes to a Work Order.
            This Queue is used to separate them into their correct Unity Forms, which
            will remove them from this Queue.  This process is done each morning on
            your Mail day.
            </p>

            

            <h3 id='Retrieval Tool'>Retrieval Tool</h3>

            <p>
            Work orders that have reached the library can only be accessed
            via the 
            <span style={{color: 'blue'}}> Retrieval tool. </span>
            Attempting to re-index documents or add documents to a Work Order residing
            in the Library can produce unexpected results. It is best to retrieve
            the Unity Form back to the Data Admin Queue with the 
            <span style={{color: 'blue'}}> Archive Retrieval tool, </span>
            accessible through the Retrieval tool.

            </p>



        </div>
    )
}

export default OnBase
