import image from '../../../../resources/images/Workflow Home.PNG'
const OnBaseWorkflowLayout = ( {selectedOption} ) => {

    const switchSelect = (param) => {
        switch(param) {
            case 'Queues':
                return <a id="Queues" href=""><rect x="0" y="175" width="385" height="490" /></a>
            case 'Data Admin Queue':
                return <a id="Data Admin Queue" href=""><rect x="0" y="420" width="385" height="20" /></a>
            case 'Waiting Originals Queue':
                return <a id="Waiting Originals Queue" href=""><rect x="0" y="442" width="385" height="20" /></a>
            case 'Document Separation Queue':
                return <a id="Document Separation Queue" href=""><rect x="0" y="295" width="385" height="20" /></a>
            case 'Inbox':
                return <a id="Inbox" href=""><rect x="0" y="665" width="385" height="490" /></a>
            case 'Related Items':
                return <a id="Related Items" href=""><rect x="390" y="790" width="1250" height="370" /></a>
            case 'Submit Button':
                return <a id="Submit Button" href=""><rect x="1150" y="60" width="90" height="90" /></a>
            case 'Secondary Viewer':
                return <a id="Secondary Viewer" href=""><rect x="390" y="175" width="1250" height="610" /></a>
            
        }
    }


    return (
        <div className = 'image-container'>
            <svg viewBox="0 0 2000 1200">
                <image href={image} width="100%"  />
                
                {switchSelect(selectedOption)}
                
                
                
            
            </svg>  
        </div>
    )
}

export default OnBaseWorkflowLayout


