
import image from '../../../../resources/images/mxi-workscope.png'
const MxiWorkscopeLayout = ( {selectedOption} ) => {

    const switchSelect = (param) => {
        switch(param) {
            case 'Work Package':
                return <a id="Work Package"><rect x="25" y="195" width="1920" height="130" /></a>
            case 'Vendor Work Package Number':
                return <a id="Vendor Work Package Number"><rect x="55" y="275" width="300" height="20" /></a>
            case 'Workscope Tab':
                return <a id="Workscope Tab"><rect x="25" y="400" width="125" height="35" /></a>
            case 'Assigned Tasks Tab':
                return <a id="Assigned Tasks Tab"><rect x="155" y="398" width="160" height="35" /></a>
            case 'Details Tab':
                return <a id="Details Tab"><rect x="323" y="398" width="88" height="35" /></a>
            case 'Set Vendor Line Number':
                return <a id="Set Vendor Line Number"><rect x="25" y="442" width="212" height="30" /></a>
            case 'Line Number':
                return <a id="Line Number"><rect x="74" y="510" width="60" height="300" /></a>
            case 'Vendor Line Number':
                return <a id="Vendor Line Number"><rect x="134" y="510" width="95" height="300" /></a>
            case 'Name':
                return <a id="Name"><rect x="229" y="510" width="973" height="300" /></a>
            case 'Config Position':
                return <a id="Config Position"><rect x="1320" y="510" width="143" height="300" /></a>
            case 'ID / Task Barcode':
                return <a id="ID / Task Barcode"><rect x="1200" y="510" width="120" height="300" /></a>
            case 'Set Vendor Line Numbers':
                return <a id="Set Vendor Line Numbers"><rect x="1150" y="60" width="90" height="90" /></a>
            case 'Secondary Viewer':
                return <a id="Secondary Viewer"><rect x="390" y="175" width="1250" height="610" /></a>
            
        }
    }


    return (
        <div className = 'image-container'>
            <svg viewBox="0 0 2000 1200">
                <image href={image} width="100%"  />
                
                {switchSelect(selectedOption)}
                
                
                
            
            </svg>  
        </div>
    )
}

export default MxiWorkscopeLayout


