import found_fault from '../../resources/images/mxi-found-fault.png'
import custom_report from '../../resources/images/mxi-custom-report.png'

const MxiProcess = () => {
    return (
        <div className='process-container'>
            <h1>MXI Process</h1>
            


            <p>
            Begin by logging into MXI in *Chrome.
            <br /><b>*Note: </b> <i>Maintenix works best in Google Chrome. </i>
            </p>

            <ol>
                <h3 id='Part 1: Finding the Work Order'>Part 1: Finding the Work Order</h3>
                <li>
                Search for the barcode tied to the Unity Form / Work Order in Maintenix.
                </li>
                <li>
                If the Vendor Work Package No. field is blank, hit 'Set Vendor Line Numbers'
                and enter the Vendor Work Package Number into the field and hit 'OK' at the 
                bottom left of the page to submit.
                </li>

                <h3 id='Part 2: Entering the Data'>Part 2: Entering the Data</h3>
                <li>
                If the Vendor Document pages have page numbers, find the first Vendor Discrepancy
                on the first page. Find its associated task in MXI by matching either the name, the
                hand-written line number, or the vendor line number.
                <br /><br />
                
                    <ul>
                        <li>
                        Note: Sometimes, a Vendor Discrepancy listed on the Vendor Documentation 
                        will not have an associated task listed in MXI. If the data is required,
                        we must add this Task and its data into the system. To do so, see 
                        <span style={{color: 'blue'}}> "Creating a Historic Fault." </span>
                        
                        </li>
                    </ul>
                </li>
                <li>
                Click on the associated Task listed in MXI to bring up the 'Task Execution'
                page for the specific Task. Under 'Labor', the Task should show one or more
                buttons labelled 'Edit Work Capture'. 
                <br /><br />
                <ul>
                        <li>
                        Note: If there are multiple 'Edit Work Capture' buttons, find the
                        one containing the "C/W --- DATA TO BE ENTERED BY DATA ENTRY" note.
                        Do this by simply clicking into each Work Capture until you've found
                        the 'Edit Work Capture' button under which the note is active.
                        
                        
                        </li>
                    </ul>
                </li>
                <li>
                	Hit the red X next to the note mentioned above and write 'CX' under the
                    Cancel Note. Type the Corrective Action provided by the Vendor Documentation
                    for the associated Task in the 'Action' field and hit 'OK'.
                    <br /> <br /> 
                    <ul>
                        <li>
                        Note: On occasion, there are steps outlined in the Vendor Documentation.
                        These steps can be listed before or after the Resolution on the
                        actual Vendor paperwork and are entirely optional. If you choose
                        to provide the data for the steps as well, add them BEFORE the 
                        final Resolution and label each item appropriately to avoid confusion.
                        <br /> <br /> 
                        For example, the correct representation of data with steps provided is as 
                        follows:
                        <br /> <br /> 
                        Step 1: This is the data for step 1 and this data is optional.
                        <br /> 
                        Step 2: This is the data for step 2 and this data is optional.
                        <br /> 
                        Resolution: This is the data for the final resolution and this data
                        is NOT optional. 
                        <br /> <br /> 

                        </li>
                        
                    </ul>
                </li>
                <li>
                        If the Task is missing its associated Vendor Line Number, you can
                        set this from the 'Task Execution' page by clicking the 'Unassigned'
                        hyperlink next to 'Vendor Line Number' under the Work Package Information
                        block towards the top of the page.
                        
                        </li>
                <li>
                        Repeat steps 2-6 until all of the data for each Discrepancy has been entered
                        into MXI.
                        
                </li>
                <h3 id='Part 3: Finalizing the Work Order'>Part 3: Finalizing the Work Order</h3>
                <ul>
                    <li>
                    On larger Work Orders or inspections, it is advised to run the 
                    'Data Entry Needed' Custom Report to ensure you did not miss any Tasks that
                    required data entry. 
                    </li>

                    <li>
                    Ensure each Task that required data entry has a Vendor Line Number associated
                    with it. The 'Set Vendor Line Numbers' button allows you to edit the 
                    Vendor Line Number for every Task in the Work Order from one page.
                    </li>

                    <li>
                    Ensure you have entered the Vendor Work Package Number for the Work Order.
                    At this point, our process in MXI is nearly complete. Navigate to the 
                    Details tab, and hit the blue '+' button next to 'History'. Make a note 
                    along the lines of "Data Entry C/W".
                    </li>

                    <li>
                    Mark the Work Order as completed in MxNet and submit the Work Order in 
                    OnBase to complete the process.
                    </li>
                    
                </ul>

                <h2 id='Additional Info'>Additional Info</h2>

                <h3 id='Creating a Historic Fault'>Creating a Historic Fault</h3>

                <p>
                Sometimes, a Vendor Discrepancy listed on the Vendor Documentation 
                will not have an associated task listed in MXI. If the data is required,
                we must add this Task and its data into the system.
                </p>

                <p>
                A rule of thumb for determining whether a Task should be created is whether
                or not the Vendor performed physical work on the aircraft or not. 
                < br/>< br/>
                For instance, if a discrepancy is written in the Vendor Documentation for 
                'acquiring tooling' or 'travelling to location', the Vendor did not perform
                work on the aircraft and these are not needed.

                < br/>< br/>
                If the discrepancy is written in the Vendor Documentation along the lines of,
                'During Service Hub Checklist, discovered L/H MLG tire in need of service'. The
                corrective action for said task likely says something to the effect of, 
                "Serviced L/H MLG tire to within limits." In this case, the vendor performed
                work on the aircraft and the task is likely not in the system. This is an example 
                of when adding a Historic Fault is necessary.
                </p>
                <p>
                    To create a Historic Fault, navigate to the 'Assigned Tasks' tab
                    and select the 'Create Historic Fault' Button. You will see something
                    similar to the following image - hover over the image to zoom in.
                </p>

                <img className='image' height='50%' width='50%' src={found_fault} />
                <p>
                    Enter the fault name exactly as it is listed on the Vendor 
                    Documentation. Copy and paste this under the Fault Description
                    as well.
                </p>
                <p>
                    Enter the data into the Corrective Action field.
                </p>
                <p>
                    The only additional data required when creating a Historic Fault
                    is the 'Failed System', which upon creation will be listed as
                    the 'Config Position' for the associated Task. Clicking on the 
                    eyeglass icon next to 'Failed System' will bring up a list of 
                    options to choose from.
                </p>

                <p>
                    We exclusively choose items listed under the section 'C-Faults'.
                    Select the option that most closely matches the system being worked 
                    in the Task. Hit 'OK' and the Task will be created. Mark the newly
                    created Task with its associated Vendor Line Number given in the 
                    Vendor Documentation to complete the creation of a Historic Fault.
                </p>

                <h3 id='Vendor Assists'>Vendor Assists</h3>

                <p>
                On occasion, a separate Vendor will assist the primary Vendor in the
                Work Package.  The most common assists are performed through Classic Interiors
                or The Leather Institute.      
                </p>

                <p>
                If this is the case, we simply enter the data provided by the main Vendor followed
                by the data provided by the Supporting Vendor that assisted on that particular task.
                <br /><br />
                If the only data provided by the main Vendor is something along the lines of
                "Refer to Classic Interiors WO XXXXXX for work performed", then this data is
                extraneous and can be skipped. Either way, it is good practice to denote where
                the data came from. For example:
                <br /> <br /> 
                        [Main Vendor Name]: This is the data provided by the main Vendor.
                        <br />
                        Classic: This is the data provided by Classic Interiors.
                        <br /> <br /> 
                
                A supporting Vendor will often have Vendor Line Numbers of their own. In the case
                where you have a Vendor Line Number provided by the Main Vendor and also the 
                Supporting Vendor for the same Task, use the Vendor Line Number provided by the 
                Main Vendor.
                        
                        
                         
                         
                </p>

                <h3 id='Custom Report'>Custom Report</h3>

                <p>
                The Custom Report is a tool that searches each Work Capture within 
                each Task for the 'C/W --- DATA TO BE ENTERED BY DATA ENTRY' tag.
                Tags that have been cancelled by you will not appear in this list.
                The image below shows where to navigate to access this report. 
                Hover over the image to zoom in for more detail.    
                </p>

                <img className='image' height='50%' width='50%' src={custom_report} />

                {/* <h3 id='Erroring a Task'>Erroring a Task</h3>

                <p>
                The Custom Report is a tool that searches each Work Capture within 
                each Task for the 'C/W --- DATA TO BE ENTERED BY DATA ENTRY' tag.
                Tags that have been cancelled by you will not appear in this list.
                The image below shows where to navigate to access this report. 
                Hover over the image to zoom in for more detail.    
                </p> */}

                

            </ol>

        </div>
    )
}

export default MxiProcess
