import image from '../../../../resources/images/MxNet Layout.PNG'
const SearchLayout = ( {selectedOption} ) => {

    const switchSelect = (param) => {
        switch(param) {
            case 'Column Headers':
                return <a id="column-headers" href=""><rect x="0" y="542" width="2000" height="35" /></a>
            case 'Tail Number':
                return <a id="Tail Number" href=""><rect x="25" y="542" width="70" height="35" /></a>
            case 'Barcode':
                return <a id="barcode" href=""><rect x="120" y="542" width="110" height="35" /></a>
            case 'Type':
                return <a id="type" href=""><rect x="255" y="542" width="70" height="35" /></a>
            case 'Location':
                return <a id="location" href=""><rect x="345" y="542" width="70" height="35" /></a>
            case 'Maintenance Provider':
                return <a id="maint-provider" href=""><rect x="500" y="542" width="130" height="35" /></a>
            case 'Fleet':
                return <a id="fleet" href=""><rect x="970" y="542" width="90" height="35" /></a>
            case 'MER Received':
                return <a id="mer-recieved" href=""><rect x="1200" y="542" width="90" height="35" /></a>
            case 'Comment':
                return <a id="comment" href="">< rect x="1385" y="542" width="90" height="35" /></a>
            case 'Date Range':
                return <a id="date-range" href=""><rect x="445" y="110" width="910" height="50" /></a>
            case 'Search Statuses':
                return <a id="search-statuses" href=""><rect x="445" y="172" width="910" height="70" /></a>
            case 'Other Options':
                return <a id="other-options" href=""><rect x="445" y="255" width="910" height="180" /></a>
            
        }
    }


    return (
        
            <div className='image-container'
            data-zoom-on-wheel="zoom-amount: 0.002;
             min-scale: 1;
              max-scale: 10;
               scaling-property: 'width/height'"
            data-pan-on-drag="button: left">
            <svg viewBox="0 0 1800 750"
            preserveAspectRatio="xMidYMid meet">
                <image href={image} width="100%"  />
                
                
                {switchSelect(selectedOption)}
                          
            
            </svg>  
            </div>
       
    )
}

export default SearchLayout


