import React, { useState } from 'react';
import styled from 'styled-components';


const Button = styled.button`
background-color: #333;
color: white;
width: 100%;
font-size: 1em;
padding: 1em;
border-radius: 5px;
margin: 0.1em 0em;
cursor: pointer;
&:disabled {
  color: grey;
  opacity: 0.7;
  cursor: default;
}
`;


const ButtonToggle = styled(Button)`
  opacity: 0.5;
  ${({ active }) =>
    active &&
    `
    opacity: 1;
  `}
`;






function ToggleGroup( {startTab, id, types, flexDir, onClickCallback} ) {
  const [active, setActive] = useState(startTab)


  const ButtonGroup = styled.div`
  display: flex;
  flex-direction: ${flexDir};
`;

  const onButtonClick = (e) => {
    

    
    setActive(() => id)
    console.log('id: ', id)
    
    onClickCallback(e.target.innerHTML)
    // onClickCallback('Overview')
    window.location.href=e.target.getAttribute("target")
    // setActive(() => 'Overview')
    setActive(() => e.target.innerHTML)
  }



  return (
  
    
    <ButtonGroup>
      {/* {console.log(`\nre-rendering id: ${id}`)}
      {console.log(`active in ${id}: ${active} `)}
      {console.log(`startTab: ${startTab}`)}
      {console.log(`active === startTab: ${active === startTab}`)}
      {console.log(`rendered: ${rendered}`)} */}
      {/* {(rendered && (active === {startTab})) ? console.log('we good.') : console.log('FALSE.') } */}
      
      
      {Object.keys(types).map((type, i) => (
        // console.log(`\ntype: ${type}`),
        // console.log(`rendered: ${rendered}`),
        // console.log(`type === 'Overview': ${type === 'Overview'}`),
        // console.log(`active === type: ${active === type}`),
        Object.keys(types).includes(active) ? null: setActive('Overview'),
        <ButtonToggle
          key={type}
          active={active === type}
          // active={({id} === 'OnBase') ? true: (active === type)}
          // active={(rendered & ({type} === 'Overview')) ? console.log(true): console.log(active === type)}
          // active={getActive(type)}
          onClick={(e) => onButtonClick(e)}
          target = {types[type]}
        >
        
          {type}
        </ButtonToggle>
      ))}
    </ButtonGroup>


  )
}

export default ToggleGroup