

const ProcessOverview = () => {


    return (
        <div className='main-overview'>

        <h3>MxNet</h3>
        <ol>
            <li>
            Mark the 5 oldest available Work Orders in MxNet 
        with your name and sequential number.  
            </li>
            <li>
            Copy the Work Order barcode from “[name] 1” and use 
        this to find the work order in OnBase.
            </li>
        </ol>

        <h3>OnBase</h3>
        <ol>
            <li>
            Open each document within the Work Order using OnBase’s
        Thumbnail viewer to ensure documents already indexed by the
        system are indexed correctly.  
            </li>
            <li>
            Re-index any documents that are mis-indexed or were not previously
        indexed by the system
            </li>
        </ol>

        <h3>Maintinex</h3>
        <ol>
            <li>
            Open the Work Order in Maintinex by searching for the
        Work Order barcode.  
            </li>
            <li>
            Enter the data from any applicable Vendor documents
        or FF-1s into their respective tasks in Maintinex.
            </li>
            <li>
            Mark the data entry as complete in Maintinex and MxNet 
        and submit the work order in OnBase. 
            </li>
        </ol>



    </div>

    )
}

export default ProcessOverview
