

const ApplicationOverview = () => {
    return (
        
        <div className='main-overview'>

            <h3>MxNet</h3>
            <p>Database containing every Work Order</p>
            <p>Provides search filters such as Date, Tail Number, 
                or Work Order Barcode</p>
            <p>Generally used to find the oldest available work 
                orders that require data entry</p>
            <h3>OnBase</h3>
            <p>Application and Database containing Work Orders and scanned copies of\
            each document within that Work Order</p>
            <p>The Work Orders reside within different queues corresponding to their\
            stage in our process</p>
            <p>Generally used to ensure we have every document associated with each\
            Work Order and that they are labeled (indexed) correctly</p>

            <h3>Maintinex</h3>
            <p>Database that contains each Work Order and their associated tasks that were\
            completed during maintenance</p>
            <p>Provides search filters such as Date, Tail Number, 
                or Work Order Barcode</p>
            <p>Data from a Work Order’s documents in OnBase is entered here</p>



        </div>
    )
}

export default ApplicationOverview
