
import '../../../Layout.css'
import { useState, useEffect } from 'react'

import ToggleGroup from '../../ToggleGroup'

import SearchLayout from './Search/Layout'
import SearchOptions from './Search/Options'
import RtsDetailsLayout from './RTS Details/Layout'
import RtsDetailsOptions from './RTS Details/Options'


const MxNetLayout = ({updateOptions, selectedOption}) => {
    const [activeLayout, setActiveLayout] = useState('Search Page')
    const types = ['Search Page', 'RTS Details Page']



    const updateActiveLayout = (layout) => {
        console.log('setting active layout..')
        setActiveLayout(() => layout)
    }

    const onLayoutButtonClick = (e) => {
        
        // console.log('e.target.value: ', e.target.value)
        // updateActiveLayout(e.target.value)
        console.log('onLayoutButtonClick e: ', e)
        updateActiveLayout(e)
    }


    const switchSelectLayout = () => {
        switch(activeLayout) {
            case 'Search Page':
                updateOptions(SearchOptions)
                return <SearchLayout selectedOption={selectedOption} />
            case 'RTS Details Page':
                updateOptions(RtsDetailsOptions)
                return <RtsDetailsLayout selectedOption={selectedOption} />
            
            
        }
    }


    return (
        <>
        
        <div className='layout-container'>
            <div className='layout-options-container'>
                <ToggleGroup types={types} onClickCallback={onLayoutButtonClick}/>
                
            </div>
            

            <div className='image-container'
            data-zoom-on-wheel="zoom-amount: 0.002;
             min-scale: 1;
              max-scale: 10;
               scaling-property: 'width/height'"
            data-pan-on-drag="button: left">
            
                {switchSelectLayout()}
            </div>

            


        </div>

        </>
    )
}

export default MxNetLayout
