
import Notes from './Notes'
import { HashLink } from 'react-router-hash-link'


const InfoPanel = ({onClick, options, links, prefix}) => {

    const onButtonClick = (e) => {
        console.log('Clicked button', e.target.value)
        window.location.replace(e.target.value)
    }

    


    return (
        <div className='info-panel-container'>
            {options ? (
                // console.log('options: ', options),
                Object.keys(options).map((option, index) => {
                    // {console.log('option: ', option)}
                    return <button id={option} value={options[option]} onClick={e => onClick(e)}>{option}</button>
                })
            ): (
            links ? (

                links.map((link, i) => {
                    return (
                    <HashLink 
                    className='link'
                    to={`/${prefix}#${link}`}                    
                    >{link}</HashLink>)
                })
            ): (
                <p>Helpful links or buttons will appear in this Info Panel.</p>
            ))}
            
                
            
            
            
            
            
            
            
       
            
        
        </div>
    )
}

export default InfoPanel
