import '../../../Layout.css'
import { useState, useEffect } from 'react'
import ToggleGroup from '../../ToggleGroup'

import OnBaseWorkflowOptions from './Workflow/Options'
import OnBaseWorkflowLayout from './Workflow/Layout'


const OnBaseLayout = ({updateOptions, selectedOption}) => {
    const [activeLayout, setActiveLayout] = useState('Workflow')
    const types = ['Workflow']


    const updateActiveLayout = (layout) => {
        console.log('setting active layout..')
        setActiveLayout(() => layout)
    }

    const onLayoutButtonClick = (e) => {
        
        // console.log('e.target.value: ', e.target.value)
        // updateActiveLayout(e.target.value)
        console.log('onLayoutButtonClick e: ', e)
        updateActiveLayout(e)
    }


    const switchSelectLayout = () => {
        switch(activeLayout) {
            case 'Workflow':
                updateOptions(OnBaseWorkflowOptions)
                return <OnBaseWorkflowLayout selectedOption={selectedOption} />
            
            
        }
    }


    return (
        <>
        
        <div className='layout-container'>
            <div className='layout-options-container'>
                <ToggleGroup types={types} onClickCallback={onLayoutButtonClick}/>
            </div>
            
            {switchSelectLayout()}


        </div>

        </>
    )
}


export default OnBaseLayout
