

import Header from './components/Header'
import SectionHeader from './components/SectionHeader'

import Nav from './components/Nav'

import ProcessOverview from './components/ProcessOverview'
import ApplicationOverview from './components/ApplicationOverview';

import MxNet from './components/MxNet/MxNet'
import MxNetProcess from './components/MxNet/MxNetProcess'
import MxNetLayout from './components/MxNet/Layouts/MxNetLayout'

import OnBase from './components/OnBase/OnBase'
import OnBaseProcess from './components/OnBase/OnBaseProcess'
import OnBaseLayout from './components/OnBase/Layouts/OnBaseLayout'


import Maintinex from './components/MXI/Maintinex'
import MxiProcess from './components/MXI/MxiProcess'
import MxiLayout from './components/MXI/Layouts/MxiLayout'


import InfoPanel from './components/InfoPanel'
import Notes from './components/Notes'

import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import { useState, useEffect } from 'react'
import './App.css';


import * as constClass from './TabData'

function App() {

  const [notes, setNotes] = useState(null)
  const [options, setOptions] = useState(null)
  const [links, setLinks] = useState(null)
  const [activeOption, setActiveOption] = useState(null)
  const [activeSubTab, setActiveSubTab] = useState('Overview')



  const assignOptions = (optionsList) => {
      // console.log('Setting options....')
      setOptions(() => optionsList)
      // console.log('options: ', options)
  }

  
  const assignLinks = (linksList) => {
    // console.log('Setting links....')
    setLinks(() => [...linksList])

    // console.log('links: ', links)
    
}

  const assignNotes = (notesList) => {
      setNotes(() => notesList)
      // console.log('notes: ', notes)
  }

  const assignActiveOption = (option) => {
    setActiveOption(() => option)
    console.log('Active Option: ', option)
}

  const onLinkButtonClick = (e) => {
    console.log('onLinkButtonClick')
    console.log('e.target: ', e.target)
    console.log('e.target.id: ', e.target.id)
    console.log('e.target.value: ', e.target.value)
    assignNotes(null)
    assignActiveOption(null)


  }

  const onOptionButtonClick = (e) => {
    console.log('onOptionButtonClick')
    console.log('e.target: ', e.target)
    console.log('e.target.id: ', e.target.id)
    console.log('e.target.value: ', e.target.value)
    assignNotes(e.target.value)
    assignActiveOption(e.target.id)

  }



  const onMainNavClick = (e) => {
    
    onContentNavClick('Overview')
    assignNotes(null)
    assignOptions(null)

  }

  const onContentNavClick = (e) => {
    setActiveSubTab(e)
    setActiveSubTab(null)
    //  console.log('Active sub tab: ', activeSubTab)
    assignNotes(null)
    assignOptions(null)

  }





  return (
    


    <div className="App">
      
      <Header />
      <Nav startTab={'Process Overview'} id='Main Nav' className = 'main-nav' routes={constClass.mainTabs} onNavClick={onMainNavClick}/>
      
      
      
      <div className="main-content">
        <InfoPanel onClick={onLinkButtonClick} />
        <Notes notes={notes} />

          
        <Switch>
            <Route exact path="/" exact component={ProcessOverview}>
            
            </Route>
                    
            <Route exact path="/Process Overview">
              <SectionHeader header='Process Overview' />
              <ProcessOverview />
              
            </Route>
          
        
            <Route exact path="/Application Overview">
              <SectionHeader header='Application Overview' />
              <ApplicationOverview />
            </Route>
       

          {/* ####################  MXNET SECTION ################## 
          ###########################################################*/}


            <Route exact path="/MXNET" >
              <SectionHeader header='MxNet Overview' />
              <InfoPanel links={constClass.mxnetOverviewLinks} prefix={'MxNet'} onClick={onLinkButtonClick} />
              <Nav startTab={'Overview'} id='MxNet' className = 'main-content-nav' prefix={'MxNet'} startTab={'Overview'} routes={constClass.mxnetTabs} onNavClick={onContentNavClick}/>
              <MxNet updateLinks={assignLinks} />
            </Route>

            <Route exact path="/MxNet/Layout Infographic">
            <SectionHeader header='MxNet Layout' />
              <InfoPanel options={options} onClick={onOptionButtonClick} />
              <Nav startTab={'Layout'} id='MxNet Layout' className = 'main-content-nav' prefix={'MxNet'} routes={constClass.mxnetTabs} onNavClick={onContentNavClick}/>
              <MxNetLayout selectedOption={activeOption} updateOptions={assignOptions} onClick={onOptionButtonClick} />
            </Route>

            <Route exact path="/MxNet/Process Guide">
            <SectionHeader header='MxNet Process Guide' />
              <InfoPanel links={constClass.mxnetProcessLinks} prefix={'MxNet/Process Guide'} onClick={onLinkButtonClick} />
              <Nav startTab={'Process'} id='MxNet Process'className = 'main-content-nav' prefix={'MxNet'} routes={constClass.mxnetTabs} onNavClick={onContentNavClick}/>
              <MxNetProcess />
            </Route>

              {/* ####################  ONBASE SECTION ################## 
              ###########################################################*/}


            <Route exact path="/ONBASE">
            <SectionHeader header='OnBase Overview' />
              <InfoPanel links={constClass.onbaseOverviewLinks} prefix={'OnBase'} onClick={onLinkButtonClick} />
              <Nav startTab={'Overview'} id='OnBase' className = 'main-content-nav'  prefix={'OnBase'} routes={constClass.onbaseTabs} onNavClick={onContentNavClick}/>
              <OnBase />
            </Route>

            <Route exact path="/OnBase/Layout Infographic">
            <SectionHeader header='OnBase Layout' />
              <InfoPanel options={options} onClick={onOptionButtonClick} />
              <Nav startTab={'Layout'} id='OnBase Layout' className = 'main-content-nav' prefix={'OnBase'} routes={constClass.onbaseTabs} onNavClick={onContentNavClick}/>
              <OnBaseLayout selectedOption={activeOption} updateOptions={assignOptions} onClick={onOptionButtonClick} />
            </Route>

            <Route exact path="/OnBase/Process Guide">
            <SectionHeader header='OnBase Process Guide' />
              <InfoPanel links={constClass.onbaseProcessLinks} prefix={'OnBase/Process Guide'} onClick={onLinkButtonClick} />
              <Nav startTab={'Process'} id='OnBase Process' className = 'main-content-nav' prefix={'OnBase'} routes={constClass.onbaseTabs} onNavClick={onContentNavClick}/>
              <OnBaseProcess />
            </Route>

            {/* ####################  MXI SECTION ################## 
            ###########################################################*/}

            <Route exact path="/Maintinex">
            <SectionHeader header='Maintenix Overview' />
            <InfoPanel links={constClass.mxiOverviewLinks} prefix={'Maintinex'} onClick={onLinkButtonClick} />
            <Nav startTab={'Overview'} id='Maintenix' className = 'main-content-nav' prefix={'Maintinex'} routes={constClass.mxiTabs} onNavClick={onContentNavClick}/>
            <Maintinex />
            </Route>

            <Route exact path="/Maintinex/Layout Infographic">
            <SectionHeader header='Maintenix Layout' />
              <InfoPanel options={options} onClick={onOptionButtonClick} />
              <Nav startTab={'Layout'} id='Maintenix Layout' className = 'main-content-nav' prefix={'Maintinex'} routes={constClass.mxiTabs} onNavClick={onContentNavClick}/>
              <MxiLayout selectedOption={activeOption} updateOptions={assignOptions} onClick={onOptionButtonClick} />
            </Route>
            <Route exact path="/Maintinex/Process Guide">
            <SectionHeader header='Maintenix Process Guide' />
              <InfoPanel links={constClass.mxiProcessLinks} prefix={'Maintinex/Process Guide'} onClick={onLinkButtonClick} />
              <Nav startTab={'Process'} id='Maintenix Process' className = 'main-content-nav' prefix={'Maintinex'} routes={constClass.mxiTabs} onNavClick={onContentNavClick}/>
              <MxiProcess />
            </Route>
        </Switch>
              

      </div>
      


    </div>

  );
}



export default App;
