import ToggleGroup2 from './ToggleGroup2'
import { useState } from 'react'

const Nav = ( {id, className, startTab, prefix, routes, onNavClick}) => {
    const [activeTab, setActiveTab] = useState(startTab)
    var flexDir = className === 'main-content-nav' ? 'column' : 'row'
    var s = {}


    const handleClick= (e) => {
        console.log('HANDLING NAV CLICK !')
        console.log('e: ', e)
        // setActiveTab(() => Object.keys(routes)[0])
        setActiveTab(() => e)
        console.log('ACTIVE TAB: ', activeTab)
        console.log('id: ', id)
        onNavClick(e)

    }

    const buildGroup = () => {
        
            Object.keys(routes).map((route, i) => (
                s[route] ? null : s[route] = '',        
                        
                prefix ? 
    
                s[route]=`#/${prefix}/${routes[route]}` : s[route]=`#/${routes[route]}`
                
            ))
        
        
        // console.log('S: ', s)
        
    }

    
    return (
        <nav className={className}>
            {buildGroup()}
            <ToggleGroup2 startTab={startTab} id={id} types={s} flexDir={flexDir} onClickCallback={handleClick}/>

            
        </nav>
    )
}

export default Nav
