export default {
    'Work Package': 'The Work Package block is present regardless of which tab below\
    you have selected (Workscope, Assigned Tasks, etc.). This provides information about\
    the current Work Order, most prominently the Vendor Work Package Number and the\
    Aircraft Type and Tail Number.',

    'Vendor Work Package Number': 'The Vendor Work Package Number is specific to the\
    Work Order and can be found on nearly every Vendor Document in the Work Order.\
    \
    \n\nOn occasion, a separate Vendor will assist the primary Vendor in the Work Package.\
    The most common assists are performed through Classic Interiors or The Leather\
    Institute.  \
    \n\nIn the case of a Vendor Assist, Classic Interiors, The Leather Institute,\
    or whomever performed the assist will have a Vendor Work Package Number of their own.\
    Under these circumstances, label the Work Order with the Vendor Work Package Number\
    given by the Primary Vendor.',

    'Workscope Tab': 'The Workscope Tab is the primary tab you will be working out of in\
    Maintenix. By default, it lays out each task ordered by Line Number. The table\
    containing each Task can hold a maximum of 500 rows before creating a second page.\
    \
    \n\n Be careful in larger inspections with >500 rows.  If you cannot find a required\
    task and decide to add it by creating a found fault, you may have missed it if you\
    neglected to check the second page and are duplicating the Task by adding it.',

    'Assigned Tasks Tab': 'The Assigned Tasks Tab lays out the same information in\
    a slightly different format with the addition of a couple useful buttons, such as\
    "Create Historic Fault" and "Mark Task As Error".\
    \
    \n\nTasks are entered into Maintenix as a hierarichal structure, with several tasks\
    often being a subtask or child of a parent task. This tab allows you to see this\
    hierarichal relationship laid out in a simple way.', 
    

    'Details Tab': 'The Details Tab contains more in depth information regarding\
    the Work Order. The only particular block of information we care about on this\
    Tab is the "History" block.\
    \n\n Upon completion of the Data Entry for a Work Order, we add a Note to the\
    History block that indicates the Data is completed (i.e. "Data Entry C/W" or \
    "Data Completed"). This is an optional step in the Data Entry process but can \
    be helpful.',

    'Set Vendor Line Number': 'Set Vendor Line Number Description',

    'Line Number': 'Line numbers compose the Workscope column in MXI under which\
    the Tasks are sorted by default, from least to greatest. It is a simple\
    sequential numbering system from 1 to the number of tasks in the Work Order.\
    To aid in locating the correct task, RTS Specialists will usually label the\
    Line Number that a task is on, placing the corresponding number next to the\
    task on the actual vendor document.',

    'Vendor Line Number': 'Vendor Line Numbers follow the order in which the\
    Discrepancies or Tasks are laid out in the actual vendor paperwork. These\
    will not necessarily match up with regular Line Numbers, except coincidentally.\
    Vendor Line Numbers compose the second column under the Workscope tab in MXI.\
    Part of our job is to ensure that every Task in MXI has its corresponding\
    Vendor Line Number in Maintinex.',

    'Name': 'This is the name of the Task and will typically match the name of\
    the corresponding Task on the actual Vendor Document. This is one of several\
    ways to ensure you are typing the data under the correct task in MXI.\
    \
    \n\n The other methods of finding the corresponding Task is to match the \
    Vendor Line Number for the Task or locate it via the Line Number written next \
    to the Task on the actual Vendor Document.',

    'ID / Task Barcode': 'The ID, or Task Barcode, is a Barcode specific to the \
    corresponding Task in the same row.  These are unique to every task across every\
    Work Order and will differ from the Work Order Barcode as well. \
    \n\n Searching in MXI with this Task Barcode will bring you to this specific task.\
    Our eventual goal as a department is to be indexing each page in a Work Order\
    down to its specific Task Barcode, if applicable.  This process is covered in the\
    Task Barcode Indexing Guide.',

    'Config Position': 'The Config Position matches the Chapter Code for the Task.\
    This is the same as the "Failed System" when creating a Historic Fault.\
    \n\n For example, the Task on Line Number 5 "# 4 MLG tire cut" has a Config Position\
    of 32-41-01-01, which corresponds with the Chapter Code 32-00 "Landing Gear".',

    
   

}