
import '../../../Layout.css'
import RangeSlider from '../../RangeSlider'
import { useState, useEffect } from 'react'
import ToggleGroup from '../../ToggleGroup'

import MxiWorkscopeOptions from './Workscope/Options'
import MxiWorkscopeLayout from './Workscope/Layout'

import MxiAssignedTasksOptions from './Assigned Tasks/Options'
import MxiAssignedTasksLayout from './Assigned Tasks/Layout'

import MxiDetailsTabOptions from './Details Tab/Options'
import MxiDetailsTabLayout from './Details Tab/Layout'

import MxiTaskExecutionOptions from './Task Execution/Options'
import MxiTaskExecutionLayout from './Task Execution/Layout'

const MxiLayout = ({updateOptions, selectedOption}) => {
    const [activeLayout, setActiveLayout] = useState('Workscope')
    const types = ['Workscope', 'Assigned Tasks', 'Details Tab', 'Task Execution']



    const updateActiveLayout = (layout) => {
        console.log('setting active layout..')
        setActiveLayout(() => layout)
    }

    const onLayoutButtonClick = (e) => {
        
        // console.log('e.target.value: ', e.target.value)
        // updateActiveLayout(e.target.value)
        console.log('onLayoutButtonClick e: ', e)
        updateActiveLayout(e)
    }


    const switchSelectLayout = () => {
        switch(activeLayout) {
            case 'Workscope':
                updateOptions(MxiWorkscopeOptions)
                return <MxiWorkscopeLayout selectedOption={selectedOption} />
            case 'Assigned Tasks':
                updateOptions(MxiAssignedTasksOptions)
                return <MxiAssignedTasksLayout selectedOption={selectedOption} />
            case 'Details Tab':
                updateOptions(MxiDetailsTabOptions)
                return <MxiDetailsTabLayout selectedOption={selectedOption} />
            case 'Task Execution':
                updateOptions(MxiTaskExecutionOptions)
                return <MxiTaskExecutionLayout selectedOption={selectedOption} />
            
        }
    }


    return (
        <>
        
        <div className='layout-container'>
            <div className='layout-options-container'>
                <ToggleGroup types={types} onClickCallback={onLayoutButtonClick}/>
                
            </div>
            
            <div className='image-container'
            data-zoom-on-wheel="zoom-amount: 0.002;
             min-scale: 1;
              max-scale: 10;
               scaling-property: 'width/height'"
            data-pan-on-drag="button: left">
            
                {switchSelectLayout()}
            </div>

            


        </div>

        </>
    )
}

export default MxiLayout
