

const MxNet = () => {

    return (
          

        <div className='overview-container'>

            
            <h1 id='Overview'>Overview</h1>

            <p>
            MxNet provides a simple database of each work order and
            is the first step in the data administration process.
            MxNet contains data in its most basic form as opposed to OnBase,
            which houses digital scans of the original physical documents.
            </p>

            <p>
            MxNet’s purpose for the Data Administration process is simply
            to locate the oldest work orders in the system that are ready
            for Data Entry.  When marking the Work Orders for Data Entry,
            we adhere to a “Take Five” rule.  
            </p>

            <p>
            “Take Five” of course means to mark your name and respective number
            on the five oldest work orders that have not yet been claimed.
            This distributes the work-load fairly and with clarity on who
            is working what and what still needs to be done. 
            </p>

            <h1 id='Work Order Types'>
                Work Order Types
            </h1>


            <p>
            Shifts are split between working Unscheduled Work Orders and
            Inspections. They are differentiated by the Type column in MxNet.
            Below is a breakdown of which Types you will work on a given day.
            </p>

            <table>
                <tr>
                    <th>Unscheduled Types</th>
                    <th>Inspection Types</th>
                    
                </tr>
                <tr>
                    <td>Unscheduled</td>
                    <td>Inspection</td>
                    
                    
                    
                </tr>
                <tr>
                    
                    <td>Line Station Mx</td>
                    <td>Conformity</td>
                    
                </tr>
                <tr>
                    
                    <td>Aircraft Exit</td>
                    <td>Paint Only</td>
                    
                </tr>
                
                <tr>
                    <td></td>
                    
                    <td>Insurance Claim</td>
                    
                    
                </tr>
                
                <tr>
                    <td></td>
                    
                    <td>Interior</td>
                    
                    
                </tr>
                </table>

                <h1 id='Work Order Age'>Work Order Age</h1>

                <p>
                    Part of our goal is to complete each Work Order in a timely manner.
                    We aim to maintain the average age of our Work Orders within a certain
                    time interval.  For the time being, this particularly applies to 
                    Unscheduled Work Orders. With the age of a Work Order defined as the 
                    time difference between the MER Received Date and the current date, 
                    we want to keep the average age of unscheduled Work Orders between 
                    <b> 3 and 5 days.</b>


                </p>
        </div>
    )
}

export default MxNet
