import image from '../../resources/images/vendor_example2.PNG'

const Mxi = ( ) => {

    
    return (
        <div className='overview-container'>
            <h1 id='Overview'>Overview</h1>

        <p>
            Maintenix, or MXI, provides us with a database of each Work 
            Order along with each vendor discrepancy contained within the
            Work Order. We access these Work Orders by searching for the 
            Barcode of the Work Order.
        </p>

            <h3 id='Tasks'> Tasks </h3>
        <p>
            <span style={{color: 'blue'}}> Tasks </span>
            make up a Work Order and are our primary focus when
            working in MXI. Tasks are what make up a Work Order, or
            Work Package. These individual tasks can range from 
            Tire Pressure Checklists to Engine Removals to something
            as simple as replacing a lost I-Pad.
        </p>

        <p>
            Under the Workscope tab for a particular Work Order in MXI,
            every task associated with the Work Order will be laid out in a
            table, sorted by Line Number as default.
        </p>

            <h3 id='Line Numbers'> Line Numbers</h3>

        
            
        <p>
            Line Numbers can be separated into two distinct types.
        
            <ul>

                <li>
                    Line Numbers
                    
                </li>
                <p>
                        Line numbers compose the Workscope column in MXI under
                        which the Tasks are sorted by default, from least to greatest.
                        It is a simple sequential numbering system from 1 to the number
                        of tasks in the Work Order. To aid in locating the correct task,
                        RTS Specialists will usually label the Line Number that a task is
                        on, placing the corresponding number next to the task on the actual
                        vendor document. 
                    </p>

                <li>
                    Vendor Line Numbers
                    
                </li>
                <p>
                        Vendor Line Numbers follow the order in which the Discrepancies or Tasks
                        are laid out in the actual vendor paperwork. These will not necessarily 
                        match up with regular Line Numbers, except coincidentally. Vendor Line 
                        Numbers compose the second column under the Workscope tab in MXI. Part of
                        our job is to ensure that every Task in MXI has its corresponding Vendor
                        Line Number in Maintenix. 
                </p>

            </ul>


        </p>

        <h3 id='Vendor Work Package Number'> Vendor Work Package Number</h3>

        <p>
            Vendor Work Package Numbers, otherwise referred to as Work Order Numbers,
            are specific to each Work Order. They can be represented in different ways 
            and locations on Vendor Documents depending on the Vendor.
            However, it can be found on most Vendor Documents in the Work Order and is
            consistent across each Vendor Document. We want to make sure that each Work Order
            is marked with the corresponding Work Order Number in the Vendor Work Package
            Number field provided by MXI.
        </p>

        <h3 id='Discrepancy Name'> Discrepancy Name </h3>

        <p>
            Each Task, or Discrepancy, will have a name associated with it. It will
            typically match the name in the Name column under the Workscope tab in MXI.
            It will also match the Task name given on the vendor documentation.
        </p>

        <h3 id='Corrective Action'> Corrective Action </h3>

        <p>
            Each Task, or Discrepancy, will also have a Corrective Action. This is the
            work that was done to resolve a specific discrepancy. They can be labelled 
            in slightly different ways depending on the Vendor, but will often be 
            presented as 'Corrective Action' or 'Resolution'. Our purpose in MXI is to 
            transfer the Corrective Action, or data, for each task into their respective
            tasks in MXI. 
        </p>

        <h3 id='Vendor Documents'> Vendor Documents</h3>
        <img className='image' height='80%' src={image} />
        <p>
            Above you will see an example of a Vendor Document. Hover over the picture
            to zoom in for more detail. In the example above, we can see that the 
            Vendor Work Package Number, labelled 'Work Order', is <br /> 
            <span style={{color: 'blue'}}>21AR421966.</span>
        </p>

        <p>
            The Vendor Line Number in this case is beneath the Work Order Number and labelled
            'Discrepancy'. This Task's Vendor Line Number is 
            <span style={{color: 'blue'}}> 1.</span>
            <br /><br />
            Coincidentally, it happens to match the regular Line Number for this Task, which
            can be seen handwritten to the right side indicating a Line Number of
            <span style={{color: 'blue'}}> 1.</span>
            <br /><br />
            It is important to remember that these numbers can, and often will, differ from
            one another.
        </p>

        <p>
            The Discrepancy or Task name in the example is simply labelled 'Discrepancy',
            and in this case is<br /> 
            <span style={{color: 'blue'}}>'050202 (EMBRAER PHENOM 300 MAINTENANCE CHECKLIST (Use 
            latest revision posted on Comply 365))'</span> 
        </p>

        <p>
            The Corrective Action in this example is labelled 'Corrective Action'. It reads<br />
            <span style={{color: 'blue'}}>
             C/W NAJ PHENOM 300 MAINTENANCE CHECKLIST REV 0 DATED 2021. 
            </span>
            <br /><br />
            This is the data that would be entered under the corresponding Task in MXI.
            <br /><br />
            
        </p>



       


        </div>
    )
}

export default Mxi
