

const MxNet = () => {
    return (
        <div className='process-container'>
            <h1>MxNet Process</h1>
            
            <p>
            Begin by logging into MxNet in *Internet Explorer.Navigate to the
            correct page by hitting 'RTS' in the green bar at the top of the page and selectng
            RTS WorkOrders Search from the drop-down menu. 
            <br /><b>*Note: </b> <i>MxNet works best in Internet Explorer </i>
            </p>

            <ol>
                <h3 id='Part 1: Prior to OnBase and Maintinex'>Part 1: Prior to OnBase and Maintinex</h3>
                <li>
                    Set the Date Range to the desired range.
                </li>
                <li>
                    Set Search Statuses to 'Waitng Data Entry' and hit the Update button.
                </li>
                <li>
                    Find the oldest Work Order corresponding to the Type of Work Order you are scheduled to work.
                </li>
                <li>
                    Click on the hyper-linked ‘MER Received Date’ in the row of that work order to bring up the RTS Details Page.
                </li>
                <li>
                	In the comments section of the RTS Details Page, mark your name followed by its sequential number
                </li>
                <li>
                	Save and close the RTS Details Page and repeat Steps 3-6 until you have marked 5 Work Orders.
                </li>
                <li>
                	Copy the barcode from the oldest marked Work Order and find the Work Order in OnBase.
                </li>
                <h3 id='Part 2: Post Indexing and Data Entry'>Part 2: Post Indexing and Data Entry</h3>
                <li>
                	Upon data completion in Maintinex and submitting the Work Order in OnBase, return to the RTS Details Page for the Work Order
                </li>
                <li>
                	Set the Data Entry Date/Time with the mini calendar icon to the current time
                </li>
                <li>
                	If the Originals Required checkbox is unchecked, also set the Approved Date/Time to the current time
                </li>
                <li>
                	Save and Close the RTS Details Page and return to Step 7.
                </li>
                <li>
                	When all 5 marked Work Orders are complete, hit the Update button and return to Step 3.
                </li>




            </ol>

        </div>
    )
}

export default MxNet
